.slidebar{
    display: flex;
    flex-direction: column;
    flex: 0.35;
}
.slidebar_header{
    display: flex;
    justify-content: space-between;
    padding: 20px;
    border-right: 1px solid lightgray;
}

.slidebar_headerRight{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 10vw;
}

.slidebar_headerRight > .MuiSvgIcon-root{
    margin-right: 2vw;
    font-size: 24px !important;
}

.slidebar_search{
    display: flex;
    text-align: center;
    background-color: #f6f6f6;
    height: 39px;
    padding: 10px;
}
.slidebar_searchContainer{
    display: flex;
    align-items: center;
    background-color: white;
    width: 100%;
    height: 35px;
    border-radius: 20px;
}
.slidebar_searchContainer > .MuiSvgIcon-root{
    color: grey;
    padding: 10px;
}
.slidebar_searchContainer >input{
    border: none;
    margin-left: 10px;
}

.slidebar_chats{
    flex: 1;
    overflow: scroll;
    height: 85vh;
    
    background-color: white;
}
